import { Context } from "./Context";
import { _useModal } from "../Hooks";
import { ModalTypes } from '../Types';

export const Provider: ModalTypes['Parts']['Provider'] = ({ children }) => {
  const modalState = _useModal();

  return (
    <Context.Provider value={modalState}>
      {children}
    </Context.Provider>
  );
};

